/* eslint camelcase: 0 */

import { MIN_PASSWORD_LENGTH } from '@/helpers/params';


export const required = (value) => value?.length > 0 || ' ';

export const min = (value) => value?.length >= MIN_PASSWORD_LENGTH || ` ${MIN_PASSWORD_LENGTH} `;

export const email = (value) => /.+@.+/.test(value) || ('    ');

export const password = (value) => /[A-Z0-9]$/.test(value) || '      ';

export const password_repeat = (pwd1, pwd2) => pwd1 === pwd2 || '  ';
