<template>
  <v-alert
    dense
    outlined
    x-small
    type="error"
    class="caption ma-0"
  >
    {{ message }}
  </v-alert>
</template>

<script>
import * as TYPES from '@/helpers/error-types';


const MESSAGES = {
  [TYPES.EMAIL_NOT_FOUND]: '  ',
  [TYPES.INVALID_PASSWORD]: ' ',
  [TYPES.USER_DISABLED]: ' ',
  [TYPES.CUSTOM_UNKNOWN_ERROR]: '- ,   ',
  [TYPES.INVALID_OOB_CODE]: ' ,   ',
  [TYPES.EXPIRED_OOB_CODE]: ' ,   ',
  [TYPES.TOO_MANY_ATTEMPTS_TRY_LATER]: '  ,  ',
  [TYPES.EMAIL_EXISTS]: '   email ',
};

export default {
  name: 'auth-error-message',
  props: {
    type: String,
  },
  computed: {
    message() {
      const { type } = this;
      if (!type) return MESSAGES[TYPES.CUSTOM_UNKNOWN_ERROR];
      return MESSAGES[type] || `: ${type}`;
    },
  },
};
</script>
